
























import { Vue, Component } from "vue-property-decorator";

@Component({
  name: "PageHeader"
})
export default class PageHeader extends Vue {
  private matchRoute: any[] = [];
  private created() {
    this.matchRoute = this["$route"].matched.map(item => {
      return { name: item.meta.title, path: item.path };
    });
  }
}
